import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router";

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        //超级管理员路由信息
        obj1: {

            path: '/admin',
            name: "admin",
            component: () => import("../views/Admin.vue"),
            children: [
                {
                path: "system",
                name: "system",
                meta: {title: '系统管理'},
                component: () => import("../views/system/System.vue"),
                children: [
                    {
                        path: "password",
                        name: "password",
                        meta: {title: '密码修改'},
                        component: () => import("../views/system/Password.vue")
                    },
                    {
                        path: "manger",
                        name: "manger",
                        meta: {title: '账号管理'},
                        component: () => import("../views/system/manger.vue")
                    }, {
                        path: "banner",
                        name: "banner",
                        meta: {title: '轮播管理'},
                        component: () => import("../views/system/Banner.vue")
                    }
                ]
            },
                {
                    path: "store",
                    name: "store",
                    meta: {title: '店铺管理'},
                    component: () => import("../views/store/Store.vue"),
                    children: [
                        {
                            path: "type",
                            name: "type",
                            meta: {title: '分类管理'},
                            component: () => import("../views/store/Type.vue")
                        }, {
                            path: "goods",
                            name: "goods",
                            meta: {
                                title: "商品管理"
                            },
                            component: () => import("@/views/store/Goods.vue")
                        }, {
                            path: "delivery",
                            name: "delivery",
                            meta: {
                                title: "配送管理"
                            },
                            component: () => import("@/views/store/Delivery.vue")
                        }
                    ]
                },
                {
                    path: "user",
                    name: "user",
                    meta: {title: '用户管理'},
                    component: () => import("../views/user/User.vue"),
                    children: [
                        {
                            path: "users",
                            name: "users",
                            meta: {title: '用户管理'},
                            component: () => import("../views/user/Users.vue")
                        }]
                },
              {
                path: "orders",
                name: "orders",
                meta: {title: '订单管理'},
                component: () => import("../views/orders/Orders.vue"),
                children: [
                  {
                    path: "order",
                    name: "order",
                    meta: {title: '订单查看'},
                    component: () => import("../views/orders/Order.vue")
                  }]
              }

            ]
        },
        //普通管理员的路由信息
        obj2: {
            path: '/admin',
            name: "admin",
            component: () => import("../views/Admin.vue"),
            children: [
                {
                    path: "system",
                    name: "system",
                    meta: {title: '系统管理'},
                    component: () => import("../views/system/System.vue"),
                    children: [
                        {
                            path: "password",
                            name: "password",
                            meta: {title: '密码修改'},
                            component: () => import("../views/system/Password")
                        }
                    ]
                }
            ]
        }
    },
    mutations: {
        loadRoutes: function (state, role) {
            if (role === '1') {
                router.addRoutes([state.obj1])
            } else {
                router.addRoutes([state.obj2])
            }

        }
    },
    actions: {},
    modules: {}
})
