<template>
    <div>
       <div class="bg">
            <el-row type="flex" justify="center" align="middle">
                <el-col :span="8" style="background: rgba(9, 31, 106, 0.71);">
                    <h3>鲜果管理系统</h3>
                    <el-row class="box">
                        <el-form ref="form"  label-width="80px" :rules="rules" :model="form">
                            <el-form-item label="用户名" prop="username">
                                <el-input v-model="form.username"></el-input>
                            </el-form-item>
                            <el-form-item label="密码" prop="password">
                                <el-input v-model="form.password" type="password"></el-input>
                            </el-form-item>
                            <el-form-item label="验证码" prop="code" style="height: 80px">
                                <el-col :span="12"> <el-input v-model="form.code"></el-input></el-col>
                                <el-col :span="11" :offset="1"><img :src="src" @click="change" style="width: 100%" height="100%" alt=""></el-col>
                            </el-form-item>
                            <el-form-item>
                                <el-button  type="primary" style="width: 100%" @click="submit">提交</el-button>
                            </el-form-item>
                        </el-form>
                    </el-row>
                </el-col>
            </el-row>
       </div>
    </div>
</template>

<script>
    import formatDate from "../utils/formatDate";
    export default {
        data() {
            return {
                form:{
                    username:"",
                    password:"",
                    code:""
                },
                rules:{
                    username:{required:true,message:'请输入用户名',trigger:'blur'},
                    password:{required:true,message:'请输入密码',trigger:'blur'},
                    code:{required:true,message:'请输入验证码',trigger:'blur'}
                },
                src:"/api/Admin/captcha"
            }
        },
        methods: {
            submit(){
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        this.axios.post("/api/admin/login",this.form).then(res=>{
                            //跨域问题
                            //浏览器像服务器发送请求的浏览器的地址和服务器的地址不同源（同源：协议+域名+端口号完全一致）HTTP协议是默认禁止这种通信的也就被称作产生了跨域问题
                            if(res.data.code==200){
                                this.$message.success("登陆成功,上次登陆的时间:"+formatDate(res.data.data.date));
                                sessionStorage.setItem('user',this.form.username);
                                let role=res.data.data.role;
                                sessionStorage.setItem('role',role);
                                this.$store.commit("loadRoutes",role);
                                this.$router.push({name:"admin"})
                            }else{
                                this.$message.error(res.data.msg);
                            }
                        })
                    }else{
                        this.$message.error("验证失败");
                    }
                })
            },
            change(){
                this.src="/api/Admin/captcha?t="+Date.now()
            }
        },
        created() {

        },
        mounted() {

        },
    }
</script>
<style scoped>
    .bg{
        width: 100vw;
        height: 100vh;
        background: url("../assets/bgimg.jpg");
    }
    .el-row{
        height: 100vh;
    }
    .box{
        margin: 0 auto;
        padding: 10px;
        width: 500px;
        height: auto;

    }
    h3{
        color: #FFF;
        line-height: 60px;
        text-align: center;

    }
    .el-form .el-form-item .el-form-item__label{
        color: #efefef;
    }
</style>